@charset 'utf-8';

/* EXTERNAL */

@import 'vendor/black-tie.css';

/* VARIABLES */

:root {
    --font-family: 'Montserrat';
    --editor-font-family: 'Source Code Pro', monospace;
    --ui-editor-background-color: #1f2225;
    --ui-base-color: #26282c;
    --ui-component-color: #2e3033;
    --ui-element-color: #3a3c40;
    --ui-active-color: #36383c;
    --ui-highlight-color: yellow;
    --ui-component-text-color: #eefafa;
    --ui-border-color: #999ca0;
    --modal-shadow: 0 0 0 5px rgba(0,0,0,0.20), 0 4px 8px 0 rgba(0,0,0,0.25);
}

/* CORE */

@import 'codemirror/lib/codemirror.css';
@import 'codemirror/addon/dialog/dialog.css';
@import 'codemirror/addon/fold/foldgutter.css';
@import 'codemirror/addon/hint/show-hint.css';

@import '_core';
@import '_editor';
@import '_divider';
@import '_map';

/* ADDONS */

/* + Menu */
@import '_filedrop';
@import '_menu-bar';
@import '_menu-dropdown';
@import '_tooltip';
@import '_modals';
@import '_shield';
@import '_typography';

/* + Widgets */
@import '_widgets';
@import '_colorpicker';

/* + Suggest */
@import '_suggest';

/* + GLSL Sandbox */
@import '_sandbox';

/* + Errors Manager */
@import '_errors';

/* + Color Palette*/
@import '_colorPalette';

/* + Map UI */
@import '_leaflet-overrides';
@import '_map-loading';
@import '_map-toolbar';
@import '_geolocator';
